
import Tabbar from '@/components/Public/Tabbar'
import AvatarCropper from 'vue-avatar-cropper'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
Tabbar, Blur, Flexbox, FlexboxItem, Card, Cell, Group, AvatarCropper
},
data: () => ({
url: '',
show: false,
per: null,
options: {
color: 'rgba(255, 255, 255, .9)',
strokeWidth: 7,
trailWidth: 7,
trailColor: 'rgba(255, 255, 255, .1)',
svgStyle: {
position: 'absolute',
top: '25px',
left: '50%',
transform: 'translateX(-50%)',
zIndex: '-1',
display: 'block',
width: '115px',
height: '115px'
}
}
}),
computed: {
isLoading: function () {
return this.$store.getters.isLoading
},
userInfo: function () {
return this.$store.getters.userInfo
},
headers: function () {
return { token: localStorage.token }
}
},
mounted () {
if (this.$isEmptyParam(localStorage.token)) {
return false
} else {
this.$store.dispatch('userInfo')
}
},
methods: {
handleUploading: function (form, xhr) {
this.show = true
const self = this
xhr.upload.onprogress = function (e) {
const loaded = e.loaded 
const total = e.total 
self.per = Math.floor((loaded / total) * 100) + '%' 
self.$refs.line.animate((loaded / total))
}
},
handleUploaded: function (response, form, xhr) {
console.log(form)
this.show = false
if (response.code === 520 && response.success) {
this.$putData(this.$configs.api.userInfo, { avatar: response.url }, response => {
if (response) {
this.$store.dispatch('userInfo')
this.$vux.toast.text('上传成功')
}
})
} else {
this.$vux.toast.text('上传失败')
}
},
avatar: function () {
this.$refs.button.click()
}
}
}



